<template>
  <div class="payment-helpbox">
    <header class="navbar">
      <div class="leadingbox">
        <a class="back" @click="goBack"><img src="/static/icons/back.png"></a>
      </div>
      <div class="titlebox">充值帮助</div>
    </header>
    <div class="listbox">
      <ul>
        <li>
          <dl>
            <dt>充值未到账怎么办？</dt>
            <dd>
              <p>充值付款后，VIP会在5分钟内生效。如遇服务器拥堵，VIP生效可能会有延迟，请耐心等待，可退出 App 重新进入查看VIP生效情况。</p>
              <p>若超过30分钟仍未到账，请点击充值页面下方的反馈按钮：</p>
              <p>1)客服支付，点击“充值反馈”按钮，提交充值反馈；</p>
              <p>2)在线支付，点击“付费未到账”按钮，提交充值未到账订单；我们会尽快为您处理！</p>
            </dd>
          </dl>
        </li>
        <li>
          <dl>
            <dt>充值需要注意什么？</dt>
            <dd>
              <p>如需充值请在充值页面发起订单，充值一次发起一笔订单，获取实时支付方式，请勿直接对历史交易账号进行“私下转账”，否则充值无法到账且不予补发。</p>
            </dd>
          </dl>
        </li>
        <li>
          <dl>
            <dt>误充或充错了账号，可以退款吗？</dt>
            <dd>
              <p>误充或充错账号后不能退款喔，建议您在充值前仔细核对充值账号与充值信息，避免发生误充、充错账号的情况。</p>
            </dd>
          </dl>
        </li>
      </ul>
    </div>
  </div>
</template>

<script>
import HttpApi from '@/httpapi.js';
import globalData from '@/global-data.js';

export default {
  name: 'payment-listing',
  data() {
    return {
      
    }
  },
  methods: {
    goBack () {
      window.history.length > 1 ? this.$router.go(-1) : this.$router.push('/');
    },
  },
  mounted() {

  },
}
</script>

<style scoped>
.paymentbox{color: #fff;}

.listbox{margin-top: 56px;}
.listbox ul{overflow: hidden;}
.listbox li{margin-top: 10px; padding: 10px 15px; background: #212429;font-size: 14px;}
.listbox dt{width: 100%; height: 30px; padding-left: 28px; line-height: 30px; color: #fff; background: url('/static/icons/icon_q.png') left center no-repeat; background-size: 20px 20px;}
.listbox dd{margin-top: 8px; width: 100%; padding-left: 28px; line-height: 22px; color: #aaa; background: url('/static/icons/icon_a.png') left top no-repeat; background-size: 20px 20px;}
</style>



